import React, { FC, useEffect } from 'react';
import classnames from 'classnames/bind';
import { Helmet } from 'react-helmet';
import { useSelector, useDispatch } from 'react-redux';

import { programPending } from 'store/program/actions';
import { getProgram, getProgramLoading } from 'store/program/selectors';

import TopLoading from 'components/TopLoading';
import Container from 'components/Container';
import SectionTitle from 'components/SectionTitle';

import TreeList from './Tree';

import styles from './styles.module.scss';

const cx = classnames.bind(styles);

const Program: FC = () => {
  const dispatch = useDispatch();
  const state = useSelector(getProgram);
  const loading = useSelector(getProgramLoading);

  useEffect(() => {
    dispatch(programPending());
  }, [dispatch]);

  return (
    <>
      <Helmet>
        <title>Novakid - Program</title>
      </Helmet>
      <TopLoading loading={loading} />
      <div className={cx('program')}>
        <Container>
          <SectionTitle title="Program" bold loading={loading} />
          <TreeList {...state} />
        </Container>
      </div>
    </>
  );
};

export default Program;
