import React, {
  FC,
  memo,
  ReactNode,
  useState,
} from 'react';
import classnames from 'classnames/bind';

import { FaChevronRight } from 'react-icons/fa';

import styles from './styles.module.scss';

const cx = classnames.bind(styles);

export interface PropsType {
  title: string;
  children?: ReactNode;
  defaultOpen: boolean;
}

const Level: FC<PropsType> = ({ title, children, defaultOpen }) => {
  const [isOpen, setIsOpen] = useState(defaultOpen);

  const handleToggle = () => {
    setIsOpen((prevOpen) => !prevOpen);
  };

  return (
    <div className={cx('level')}>
      <button className={cx('button')} type="button" onClick={handleToggle}>
        <FaChevronRight color="#4a4a4a" className={cx('arrow', { arrow__open: isOpen })} />
        <span className={cx('buttonText')}>{title}</span>
      </button>
      {isOpen ? children : null}
    </div>
  );
};

export default memo(Level);
