import React, { FC, memo } from 'react';
import classnames from 'classnames/bind';

import { ProgramLevelType, ProgramUnitType, ProgramLessonType } from 'types/programType';

import Level from './Level';
import Unit from './Unit';
import Lesson from './Lesson';

import styles from './styles.module.scss';

const cx = classnames.bind(styles);

export interface PropsType {
  levels: ProgramLevelType[];
  units: ProgramUnitType;
  lessons: ProgramLessonType;
}

const TreeList: FC<PropsType> = ({ levels, units, lessons }) => {
  return (
    <div className={cx('tree')}>
      {levels.map((level, index) => (
        units[level.id]?.length ? (
          <Level
            key={level.id}
            title={`Level ${level.number} - ${level.name}`}
            defaultOpen={index === 0}
          >
            <div className={cx('units')}>
              {units[level.id]?.map((unit) => (
                lessons[unit.id]?.length
                  ? (
                    <Unit title={unit.name} key={unit.id}>
                      <div className={cx('lessons')}>
                        {lessons[unit.id]?.map((lesson) => (
                          <Lesson key={lesson.id} title={lesson.name} />
                        ))}
                      </div>
                    </Unit>
                  ) : null
              ))}
            </div>
          </Level>
        ) : null
      ))}
    </div>
  );
};

export default memo(TreeList);
