import { AppStateType } from 'types';
import ProgramState from 'types/programType';

export const getProgramLoading = (state: AppStateType): boolean => (
  state.program.levelLoading
  || state.program.unitLoading
  || state.program.lessonLoading
  || state.program.topLoading
);

export const getProgram = (state: AppStateType): Pick<ProgramState, 'levels' | 'units' | 'lessons'> => ({
  levels: state.program.levels,
  units: state.program.units,
  lessons: state.program.lessons,
});
